   ========================================================================== */
:root {
  --color__primary: #003255;
  --color__primary--contrast: #ffffff;
  --color__secondary: #cc0018;
  --color__secondary--contrast: #ffffff;
  --color__bg: #ffffff;
  --color__text: #333333;
  --color__text--contrast: #ffffff;
  --color__gray--light: #f3f3f3;
  --padding: 20px;
  --background: 0px 0px 4px 1px rgba(0, 0, 0, 0.25);
}

