* {
  box-sizing: border-box;
}

*:focus {
  outline: 2px dotted var(--color__secondary);
}

body {
  font-family: 'Open Sans', sans-serif;
  color: var(--color__text);
  background: var(--color__gray--light);
  margin: 0;
}

main {
  width: 100%;
}

img {
  max-width: 100%;
  height: auto;
}

h1,
h2,
h3 {
  margin: 0;
}

a {
  color: var(--color__secondary);
  text-decoration: none;
  font-weight: 600;
}

.link--effect {
  text-decoration: underline 0.15em rgba(0, 0, 0, 0);
  transition: color, text-decoration-color 0.3s ease-in-out;
}

.link--effect:hover {
  text-decoration-color: #cc0018;
}

a > img:only-child {
  display: block;
}

/* ==========================================================================
   Layout - styles defining the layout of the page
   ========================================================================== */
footer {
  margin-top: 20px;
  background: var(--color__primary);
  color: var(--color__primary--contrast);
  padding: var(--padding);
  -webkit-box-shadow: var(--background);
  box-shadow: var(--background);
  display: flex;
  justify-content: space-between;
}

header {
  background: var(--color__bg);
  -webkit-box-shadow: var(--background);
  box-shadow: var(--background);
  margin-bottom: 20px;
}

.header__top {
  display: flex;
  padding: var(--padding);
}

.header__logo {
  max-width: 200px;
  margin: 0 20px;
}

.header__right {
  margin-left: 50px;
  margin-top: 20px;
}

.header__title {
  font-size: 2.4em;
  margin: 0;
  color: var(--color__primary);
}

.header__subtitle {
  font-size: 1.5em;
  color: var(--color__primary);
}

/* ==========================================================================
   Modules - independent modules, that can be used multiple times
   ========================================================================== */
.content {
  background: var(--color__bg);
  -webkit-box-shadow: var(--background);
  box-shadow: var(--background);
}

.content__title {
  padding: var(--padding);
}

.content__text {
  padding: var(--padding);
}

.form {
  display: flex;
}

.form input[type='text'] {
  padding: 5px;
  border: 1px solid var(--color__primary);
  width: 66%;
  margin-right: 5px;
}

.form input[type='submit'] {
  padding: 6px;
  width: 33%;
  cursor: pointer;
  background: var(--color__primary);
  color: var(--color__primary--contrast);
  transition: background 0.3s;
  border: none;
}

.form input[type='submit']:hover {
  background: var(--color__secondary);
}

.nav-footer__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.nav-footer__link {
  color: var(--color__primary--contrast);
  font-weight: 400;
}

.nav-footer__link .fa {
  margin-right: 5px;
}

.nav-desktop {
  /** 2nd level */
}

.nav-desktop .menu {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.nav-desktop .menu-item {
  position: relative;
  width: 100%;
  text-align: center;
}

.nav-desktop .menu-item a {
  transition: background 0.3s;
  background: var(--color__primary);
  color: var(--color__primary--contrast);
  font-weight: 400;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.nav-desktop .menu-item a:hover, .nav-desktop .menu-item a:focus {
  background: var(--color__secondary);
}

.nav-desktop .sub-menu {
  list-style-type: none;
  margin: 0;
  padding: 0;
  position: absolute;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.nav-desktop .sub-menu .menu-item {
  display: none;
  position: relative;
}

.nav-desktop .menu-item-has-children:hover .sub-menu .menu-item,
.nav-desktop .menu-item-has-children.expanded .sub-menu .menu-item {
  display: block;
  top: 0;
}

.side-box {
  margin-top: 20px;
  padding: var(--padding);
  background: var(--color__bg);
  -webkit-box-shadow: var(--background);
  box-shadow: var(--background);
}

/*Flex items*/
.teaser-section {
  display: flex;
  flex-wrap: wrap;
}

.teaser {
  display: flex;
  background: var(--color__bg);
  -webkit-box-shadow: var(--background);
  box-shadow: var(--background);
  margin-top: 20px;
  margin-right: 0;
  width: 100%;
}

@media all and (min-width: 40em) {
  .teaser {
    width: 50%;
    margin-right: 20px;
  }
  .teaser:nth-child(2n) {
    margin-right: 0;
  }
}

@media all and (min-width: 60em) {
  .teaser {
    width: 32.2%;
    margin-right: 1.7%;
  }
  .teaser:nth-child(2n) {
    margin-right: 1.7%;
  }
  .teaser:nth-child(3n) {
    margin-right: 0;
  }
}

.teaser__content {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.teaser__link {
  flex: 1 0 auto;
}

.teaser__text {
  padding: 0 var(--padding);
  color: var(--color__text);
  font-weight: 400;
}

.teaser__title {
  margin: 10px 0;
  padding: 0 var(--padding);
  color: var(--color__primary);
}

.teaser__img {
  max-width: 100%;
}

.teaser__button {
  padding: 6px 16px;
  cursor: pointer;
  background: var(--color__primary);
  color: var(--color__primary--contrast);
  transition: background 0.3s;
  border: none;
  text-decoration: none;
  font-weight: 400;
}

.teaser__button:hover {
  background: var(--color__secondary);
}

.teaser__meta {
  padding: 0 var(--padding);
  margin: 20px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.teaser__meta span {
  font-size: 0.9em;
}

.gallery {
  margin: 0 auto 18px;
}

.gallery .gallery-item {
  float: left;
  margin-top: 0;
  text-align: center;
  width: 33%;
  padding-right: 5px;
  box-sizing: border-box;
}

.gallery-columns-2 .gallery-item {
  width: 50%;
}

.gallery-columns-4 .gallery-item {
  width: 25%;
}

.gallery img {
  box-shadow: 0px 0px 4px #999;
  border: 1px solid white;
  padding: 8px;
  background: #f2f2f2;
}

.gallery img:hover {
  background: white;
}

.gallery-columns-2 .attachment-medium {
  max-width: 92%;
  height: auto;
}

.gallery-columns-4 .attachment-thumbnail {
  max-width: 84%;
  height: auto;
}

.gallery .gallery-caption {
  color: #888;
  font-size: 12px;
  margin: 0 0 12px;
}

.gallery dl,
.gallery dt {
  margin: 0;
}

.gallery br + br {
  display: none;
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
}

